import React from "react";

export const Card = (props) => {
  let cardItem = props.cardItem;
  let src = cardItem.thumbnail.href;
  let title = cardItem.name;
  let price = cardItem.price
    ? cardItem.price.selling
    : cardItem.priceRange.selling.high;

  return (
    <div
      onClick={() => props.onItemClick(props)}
      className="card mx-auto"
      style={{ width: "24rem", padding: 0 }}
    >
      <img src={src} className="card-img-top" alt={props.alt} />
      <h5
        className="card-title"
        style={{
          position: "absolute",
          backgroundColor: "#ECECECEA",
          paddingTop: 10,
          paddingBottom: 10,
          paddingLeft: 10,
          paddingRight: 10,
          width: "100%",
        }}
      >
        {title}
      </h5>
      <h5
        className="card-title"
        style={{
          position: "absolute",
          bottom: "1rem",
          backgroundColor: "#000000AA",
          color: "white",
          marginLeft: "1rem",
          padding: 5,
        }}
      >
        $ {price}
      </h5>
    </div>
  );
};

