import React, { Component } from "react";
import { Card } from "./Card";
import { withRouter } from "react-router-dom";
import { Loader } from "../../components/Loader";
import { fetchProducts } from "../../apis/products";
import { ProductModal } from "./ProductModal";

class Home extends Component {
  state = {
    data: { groups: [] },
    isLoading: false,
    productModalVisible: false,
    selectedProduct: null,
  };

  componentDidMount() {
    this.setState({ isLoading: true });
    fetchProducts().then((res) => {
      res.json().then((response) => {
        this.setState({
          data: response,
          isLoading: false,
        });
      });
    });
  }

  hideModal = () => {
    this.setState({ productModalVisible: false });
  };

  onItemClick = (item) => {
    this.setState({ selectedProduct: item, productModalVisible: true });
    // this.props.history.push("/product");
  };

  render() {
    var cards;

    if (this.state.data) {
      cards = this.state.data.groups.map((cardItem) => (
        <Card
          cardItem={cardItem}
          onItemClick={this.onItemClick}
          className="mx-auto"
          key={cardItem.id}
        />
      ));
    }
    return (
      <div className="container">
        {this.state.isLoading ? (
          <Loader />
        ) : (
          <div>
            <ProductModal
              productItem={this.state.selectedProduct}
              hideModal={this.hideModal}
              productModalVisible={this.state.productModalVisible}
            />
            <div className="row g-3">{cards}</div>
          </div>
        )}
      </div>
    );
  }
}

export default withRouter(Home);
